import noUiSlider from 'nouislider';

const filterOpenBtn = document.querySelector('.filters__button-open');
const filterCloseBtn = document.querySelector('.filters__button-close');
const filterPopover = document.querySelector('.filters__popover');
const panelButtons = document.querySelectorAll('.filters__panel-title');

// ренж-слайдер
const priceFrom = document.getElementById('price-from');
const priceTill = document.getElementById('price-till');
const rangeSlider = document.getElementById('slider');

// инициализация
noUiSlider.create(rangeSlider, {
  start: [500, 7650],
  step: 50,
  connect: true,
  range: {
    'min': 0,
    'max': 10000
  }
});

// привязка к полям
rangeSlider.noUiSlider.on('update', function (values, handle) {
  let value = values[handle];

  if (handle) {
    priceTill.value = Math.round(value); // с округлением до целого
  } else {
    priceFrom.value = Math.round(value);
  }
});

priceTill.addEventListener('change', function () {
  rangeSlider.noUiSlider.set([this.value, null]);
});

priceFrom.addEventListener('change', function () {
  rangeSlider.noUiSlider.set([null, this.value]);
});

// открытие/закрытия фильтра
filterOpenBtn.addEventListener('click', () => {
  filterPopover.classList.remove('filters__popover--closed');
});

filterCloseBtn.addEventListener('click', () => {
  filterPopover.classList.add('filters__popover--closed');
});

// открытие панелей в фильтрах
panelButtons.forEach((button) => {
  button.addEventListener('click', () => {
    button.classList.toggle('filters__panel-title--active');
  });
});
